.wrapper {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: #FFFFFF;
  border-radius: 1em;
  padding: 1em;
  transform: translateY(100%);
  opacity: 0;
  visibility: hidden;
  transition-property: opacity, transform, visibility;
  transition-duration: .3s;
  display: flex;
  flex-direction: column;
}

.wrapper.isActive {
  transform: none;
  opacity: 1;
  visibility: visible;
}

.products {
  flex-grow: 1;
  overflow: auto;
}

.deliveryDetails {
  margin: 0 -1em;
  padding: 1em 1em 1.5em;
  background-color: #F5F5F5;
}

.deliveryDetailsTitle {
  font-size: 1.25em;
  font-weight: 600;
  margin-bottom: .4em;
}

.deliveryDetailsDivider {
  margin: 0.75em -0.5em 0.5em;
  height: 1px;
  background-color: #e9e9e9;
}




