@import '../../common';

$item-spacing: .25em;

.wrapper {
	@include horizontal-scroll;
	margin: 0 -1em; }

.in {
	display: flex; }

.item {
	position: relative;
	padding: 0 $item-spacing;
	display: flex;

	&:first-child {
		padding-left: 1em; }

	&:last-child {
		padding-right: 1em; }

	&In {
		position: relative;
		padding: .75em;
		border-radius: .3em;
		border: 1px solid #c5c5c5;
		background-color: #f0f0f0;
		color: #9b9b9b;
		display: flex;
		flex-direction: column;
		justify-content: center;
		white-space: nowrap;
		transition-property: background-color, color;
		transition-duration: .1s;

		.isActive & {
			border-color: #128bff;
			background-color: #e0f0ff;
			color: #128bff; }

		.viewRich & {
			justify-content: flex-start; } } }

.action {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	opacity: 0; }

.title {
	font-weight: 600; }

.note {
	font-size: .8em;

	.title + & {
		margin-top: .5em; } }



