.wrapper {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	display: flex;
	align-items: flex-end;
	pointer-events: none; }

.spacer,
.in {
	height: 4em; }

.spacer {
	margin-top: -1em; }

.in {
	width: 100%;
	position: relative;
	position: sticky;
	bottom: 0; }

.content {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	overflow: hidden;
	background-color: #FFFFFF;
	border-bottom-left-radius: 1em;
	border-bottom-right-radius: 1em;
	pointer-events: auto;
	box-shadow: 0 -2px 4px rgba(#000000, .1);
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 1em;
	transform: translateY(100%);
	opacity: 0;
	visibility: hidden;
	transition-property: transform, opacity, visibility;
	transition-duration: .3s;

	.isActive & {
		transform: none;
		opacity: 1;
		visibility: visible; } }



