@import '../../common';

.wrapper {
	width: $product-width;
	flex-shrink: 0;
	color: #DDDDDD;
	transition: color .1s; }

.image {
	position: relative;
	margin: 0 auto;
	overflow: hidden;

	&Frame {
		position: relative;

		&::before {
			content: '';
			display: block;
			padding-top: calc(100% / var(--product-image-aspect-ratio)); } }

	&In {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		object-fit: cover;
		object-position: center bottom;
		transition: opacity .3s;
		opacity: 0;

		.isRealVisible &.viewReal,
		.isPlaceholderVisible &.viewPlaceholder {
			opacity: 1; } } }

.info {
	position: relative;
	margin-top: $product-info-top-space;
	padding: 2em 1em 1em;
	color: #000000;

	&In {
		position: relative;
		z-index: 1;
		display: flex;
		flex-wrap: wrap; } }

.viewShadow {
	.name,
	.price,
	.note {
		&::before {
			content: '';
			height: .8em;
			background-color: currentColor;
			border-radius: .1em;
			display: inline-block;
			max-width: 100%; } } }

.name {
	margin: 0 .5em 0 0;

	&::before {
		width: 6em;
		opacity: .22; } }

.price {
	margin: 0;
	position: relative;
	top: .1em;

	&::before {
		width: 4em;
		opacity: .1; } }

.note {
	width: 100%;
	margin: .5em 0 0;

	&::before {
		width: 15em;
		opacity: .1; } }

.addToCart {
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	width: 100%;
	height: 100%;
	opacity: 0;
	z-index: 1; }



