@import '../../common';

.wrapper {
	position: relative; }

.counter {
	@include horizontal-scroll;
	margin-top: 3em;

	&::-webkit-scrollbar {
		width: 0;
		height: 0;
		background: transparent; }

	&In {
		display: flex;

		&::before,
		&::after {
			content: '';
			width: (100% - $product-width) / 2;
			display: block;
			flex-shrink: 0; } } }

.background {
	content: '';
	position: absolute;
	left: 0;
	right: 0;
	bottom: 0;
	top: 0;
	display: flex;
	flex-direction: column;

	&::before {
		content: '';
		width: 100%;
		padding-top: calc(#{$product-width} / var(--product-image-aspect-ratio)); }

	&::after {
		content: '';
		width: 100%;
		margin-top: $product-info-top-space;
		flex-grow: 1;
		background-color: #F5F5F5; } }



