:global body,
:global html {
  margin: 0;
  padding: 0;
}

:global body {
  background-image: url("./background.jpg");
  background-position: center center;
  background-size: cover;
  background-attachment: fixed;
  font-family: Arial, 'Helvetica Neue', Helvetica, sans-serif;
}

:global h1,
:global h2,
:global h3,
:global h4,
:global h5,
:global input,
:global textarea {
  font-family: inherit;
}

:global *,
:global *::before,
:global *::after {
  box-sizing: border-box;
}

:global button {
  cursor: pointer;
}

.wrapper {
  padding: 1em;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

.in {
  position: relative;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.98);
  padding: 1em;
  border-radius: 1em;
}




