$transition-duration: .3s;

.wrapper {
	position: relative;
	display: flex;
	margin: 0 auto;
	max-width: 40em; }

.section {
	border: none;
	background: none;
	outline: none;
	font-size: 1.1em;
	color: #c5c5c5;
	padding: .5em 0;
	transition: color $transition-duration;

	&.isActive {
		color: #000000; } }

.section,
.bar {
	width: (100% / 3); }

.bar {
	position: absolute;
	bottom: 0;
	left: 0;
	border-bottom: .1em solid;
	transition: transform $transition-duration;

	@for $i from 1 through 3 {
		.section:nth-child(#{$i}).isActive ~ & {
			transform: translateX(#{($i - 1) * 100%}); } } }



