.wrapper {
  position: relative;
  display: flex;
  margin: 0 auto;
  max-width: 40em;
}

.section {
  border: none;
  background: none;
  outline: none;
  font-size: 1.1em;
  color: #c5c5c5;
  padding: .5em 0;
  transition: color 0.3s;
}

.section.isActive {
  color: #000000;
}

.section,
.bar {
  width: 33.33333%;
}

.bar {
  position: absolute;
  bottom: 0;
  left: 0;
  border-bottom: .1em solid;
  transition: transform 0.3s;
}

.section:nth-child(1).isActive ~ .bar {
  transform: translateX(0%);
}

.section:nth-child(2).isActive ~ .bar {
  transform: translateX(100%);
}

.section:nth-child(3).isActive ~ .bar {
  transform: translateX(200%);
}




