:global {
	body,
	html {
		margin: 0;
		padding: 0;
	}

	body {
		background-image: url('./background.jpg');
		background-position: center center;
		background-size: cover;
		background-attachment: fixed;
		font-family: Arial, 'Helvetica Neue', Helvetica, sans-serif;
	}

	h1,
	h2,
	h3,
	h4,
	h5,
	input,
	textarea {
		font-family: inherit;
	}

	*,
	*::before,
	*::after {
		box-sizing: border-box;
	}

	button {
		cursor: pointer;
	}
}

.wrapper {
	padding: 1em;
	display: flex;
	position: fixed;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
}

.in {
	position: relative;
	width: 100%;
	background-color: rgba(#ffffff, 0.98);
	padding: 1em;
	border-radius: 1em;
}



